@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
@import '../../style.css';
:root {
    /*Config title-page*/
    --title-page-servizi-background-image: url('./../assets/background2.webp');
    --title-page-servizi-background-color: hsla(273, 84%, 42%, 0.95);
    --title-page-servizi-color: white;
    --title-page-servizi-font-size-h2: 4em;
    --title-page-servizi-font-size-p: 1.5em;
    
    /* Section Archive card */
    --archive-card: 25%;
    --archive-card-background: white;
    --archive-card-h3: 3em;
    --archive-card-p: 1.2em;
    --border-radius: 10px;
    /* Section Price */
    --price-column: 33%;
    --price-column-background: white;
    --price-h3: 2em;
    --price-price: 20px;
    --price-plan: 30px;
  }
  
  /* Section intro-site */
  .title-page-servizi {
    background-image: var(--title-page-servizi-background-image);
    background-color: var(--title-page-servizi-background-color);
    background-blend-mode: overlay;
    background-position: center;
    background-size: cover;
    text-align: center;
    color: var(--title-page-servizi-color);
    padding: 10em 0;
  }
  
  .title-page-servizi h2 {
    font-size: var(--title-page-servizi-font-size-h2);
    color: white;
    text-shadow: 1px 1px 1px black;
  }
  
  .title-page-servizi p {
    font-size: var(--title-page-servizi-font-size-p);
    color: white;
  }

 
  .modal-content {
    background-color: rgba(0, 0, 0, 0.9);
    color: white;
    z-index: 9999999;
    position: fixed;
    width: calc(100% - 4em);
    height: 100%;
    max-height: 90vh;
    overflow: auto;
    top: 0;
    left: 0;
    padding: 2em;
    margin: 0;
  }
  


  .modal-content span{
    float:right!important;
    font-size: 2em;
    background-color: transparent;
    border:none;
    color:white;
  }

.modal-content .price{
  font-size: 4em;
}


.modal-card-img {
  display: block;
  float: left;
  width: 30%;
  margin-top: 5em;
}
.modal-card-info{
  display: block;
  float: left;
  width: 70%;
}

.modal-card-info h2{
}

.modal-content img {
  max-width: calc(100% - 5em);
  max-height: 100%;
  margin: 0 5em 0 0;
  float: left;
}

  .modal-plan .add-to-cart,.modal-plan .price {
    display: none;
}

button.btn.btn-trans.add-to-cart {
  margin-top: 2em;
}

.modal-list-plan, .modal-list-plan {
  width: calc(32% - 3em);
  padding: 1em;
  margin: 1em 1em 0 0;
  border: 1px solid white;
  float: left;
}


  .close-cart-btn{
    float:right!important;
    font-size: 1em;
    background-color: transparent;
    border:none;
    color:white;
  }
  
 
  /* Cart section */
  .cart {
    display: none;
    border: none!important;
    padding: 10px;
    margin-top: 10px;
    z-index: 9999;
    border: none!important;
}
  
  .cart p {
    margin: 30px 0 0 0px;
  }
  
  
  button.cart-toggle-btn {
    position: fixed;
    top: 30%;
    right: 0;
    padding: .5em;
    border: 0px;
    font-size: 1.5em;
    background: black;
    color: white;
  }
  
  .cart-toggle-btn:hover {
    background-color: #0056b3;
  }


.cart.open {
  background-color: rgba(0, 0, 0, 0.9);
  border: none !important;
  color: #fff;
  display: block;
  font-size: 1.2em;
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 2em;
  position: fixed;
  top: 0;
  width: calc(100% - 4em);
  max-height: 90vh;
  overflow: auto;
}

.filter{
  max-width: 350px;
  margin: 0 auto;
}

.filter select{
  width: 40% !important;
  float: left;
}
.filter input {
  width: 60% !important;
  float: left;
}



@media screen and (max-width: 1024px) {
  .modal-card-info, .modal-card-img {
    width: 100%;
    margin: 0;
  }
  .modal-content img {
    margin: 0 0 5em 0;
  }
  .archive-card .card {
    position: relative;
    overflow: hidden;
    padding: 1em;
    margin: .5em;
    width: calc(50% - 3em) !important;
    float: left;
}
}